
#drum-machine {
  text-align: center;
  
}
@keyframes swing {
  0% { transform: translateX(0); }
  50% { transform: translateX(200px); }
  100% { transform: translateX(0); }
}

h1 {
  font-family: 'Roboto', sans-serif; /* Cambia 'Roboto' por la fuente que prefieras */
  font-size: 2em; /* Tamaño del texto */
  color: #41464a;
  text-shadow: 3px 3px 0 #c8d2dd, 5px 5px 0 #41464a;
  animation: swing 2s ease-in-out infinite; /* 2s de duración, se repite infinitamente */
}

.drum-pads {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap:20px;
  grid-template-rows: 1fr 1fr 1fr;
  margin: 50px auto;
  width: min-content;
}

.drum-pad{
  width: 100px;
  border: 2px solid #2f2f2f;
  background-color: mediumpurple;
  color: black;
  font-size: 20px;
  border-radius: 8px;
  box-shadow: 4px 4px 10px;
  transition: all 0.5s ease-in-out;
  font-family: "Roboto";
}
.drum-pad:active{
  transform: translate(4px,4px);
}

#display{
  padding: 10px; /* Añadir un espacio alrededor del contenido */
  /* Otros estilos opcionales */
  font-family: 'Roboto', sans-serif; /* Fuente del texto */
  width: 100px;
  
  border: 2px solid #2f2f2f;
  background-color: mediumpurple;
  margin-left: 45%;
  color: black;
  font-size: 20px;
  border-radius: 8px;
  box-shadow: 4px 4px 10px;

}